import { isWebkit, isIE, isEdge } from '../helpers'

export default function sayHello() {
  const staticText = 'Made with ❤️ by Demiweb'
  const staticUrl = '> https://demiweb.pro/'
  const hello = [
    `\n %c ${staticText} %c ${staticUrl} %c %c \n\n`,
    'border: 1px solid #000;color: #fff; background: #3d2579; padding:5px 0;',
    'color: #fff; background: #ffd800; padding:5px 0;border: 1px solid #000;',
    'background: #fff; padding:5px 0;',
    'color: #b0976d; background: #fff; padding:5px 0;',
  ]

  if (isIE || isEdge) {
    window.console.log(`${staticText} - ${staticUrl}`)
  } else if (isWebkit) {
    let title = '%c'

    for (let i = 0; i < 12; i++) {
      title += ' '
    }

    window.console.log.apply(console, hello)
    window.console.log(
      title,
      'font: 36px/100% sans-serif; background-repeat: no-repeat; background-size: 100%; background-image: url(data:image/svg+xml;base64,);'
    )
  } else {
    const title = 'demiweb'
    const banner = `\n%c${title.split('').join('%c %c')}%c\n`
    const styler = []

    const symbol = [
      'font: 32px/1.5 Helvetica, Arial, sans-serif',
      'text-transform: uppercase',
      'color: #fff',
      'background-color: #ffd800',
      'padding: 4px 14px',
    ].join(';')

    title.split('').forEach(() => {
      styler.push(symbol)
      styler.push('')
    })

    window.console.log(`${staticText} - ${staticUrl}`)
    window.console.log(banner, ...styler)
  }
}
